import { Node, Block, Inline, Text } from '@ali/4ever-cangjie';
import { Paragraph } from '@ali/4ever-plugin-paragraph';

export default function newNode(node: Node): Node {
  if (node.isElement() && node.isBlock()) {
    return Block.fromJSON(node.toJSON());
  }
  if (node.isElement() && node.isInline()) {
    return Inline.fromJSON(node.toJSON());
  }
  if (node.isText()) {
    return Paragraph.create({
      nodes: [Text.fromJSON(node.toJSON())],
    });
  }
  return Text.create('');
}
